import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';

const SimpleCreditTest = () => {
  const [result, setResult] = useState('');
  const [loading, setLoading] = useState(false);

  const testCredits = async () => {
    setLoading(true);
    setResult('Testing...\n');
    
    try {
      // Step 1: Direct API call without axios
      setResult(prev => prev + 'Step 1: Demo login...\n');
      
      const loginResponse = await fetch('http://localhost:5001/api/auth/demo-login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: '{}'
      });
      
      const loginData = await loginResponse.json();
      setResult(prev => prev + `Login success: ${loginData.success}\n`);
      setResult(prev => prev + `User ID: ${loginData.data.user.id}\n`);
      
      const token = loginData.data.token;
      setResult(prev => prev + `Token: ${token.substring(0, 30)}...\n\n`);
      
      // Step 2: Get credits
      setResult(prev => prev + 'Step 2: Get credit balance...\n');
      
      const balanceResponse = await fetch('http://localhost:5001/api/credits/balance', {
        headers: { 
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });
      
      const balanceData = await balanceResponse.json();
      setResult(prev => prev + `Balance API success: ${balanceData.success}\n`);
      setResult(prev => prev + `Available credits: ${balanceData.data.available_credits}\n`);
      setResult(prev => prev + `Total credits: ${balanceData.data.total_credits}\n`);
      setResult(prev => prev + `Used credits: ${balanceData.data.used_credits}\n\n`);
      
      // Step 3: Test with proxy (frontend URL)
      setResult(prev => prev + 'Step 3: Test with frontend proxy...\n');
      
      const proxyResponse = await fetch('/api/credits/balance', {
        headers: { 
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });
      
      const proxyData = await proxyResponse.json();
      setResult(prev => prev + `Proxy API success: ${proxyData.success}\n`);
      setResult(prev => prev + `Proxy available credits: ${proxyData.data.available_credits}\n\n`);
      
      // Final result
      if (parseFloat(balanceData.data.available_credits) > 0) {
        setResult(prev => prev + 'âœ… SUCCESS: You have credits! Frontend issue confirmed.\n');
        setResult(prev => prev + `ðŸŽ¯ You should see ${balanceData.data.available_credits} credits in topbar\n`);
      } else {
        setResult(prev => prev + 'âŒ PROBLEM: API returning 0 credits\n');
      }
      
    } catch (error) {
      setResult(prev => prev + `âŒ ERROR: ${error.message}\n`);
    }
    
    setLoading(false);
  };

  useEffect(() => {
    testCredits();
  }, []);

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Simple Credit Test</h1>
      
      <Button onClick={testCredits} disabled={loading} className="mb-4">
        {loading ? 'Testing...' : 'Run Test Again'}
      </Button>
      
      <div className="bg-gray-100 p-4 rounded-lg">
        <pre className="whitespace-pre-wrap text-sm font-mono">
          {result}
        </pre>
      </div>
      
      <div className="mt-4 p-4 bg-blue-50 rounded-lg">
        <h3 className="font-semibold mb-2">What this test does:</h3>
        <ul className="text-sm space-y-1">
          <li>1. Direct API call to login (bypassing frontend)</li>
          <li>2. Direct API call to get credits (bypassing frontend)</li>
          <li>3. Test with frontend proxy</li>
          <li>4. Compare results</li>
        </ul>
      </div>
    </div>
  );
};

export default SimpleCreditTest;
